* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 14pt;
  margin: 0;
  padding: 0;
}
h1 {
  background-color: black;
  color: white;
  margin-bottom: 0;
  min-height: 80px;
  line-height: 1.5;
  border-radius: 7px;
  text-align: center;
  padding-left: 7px;
  padding-right: 7px;
}
table {
  text-align: center;
}
thead th {
  color: yellow;
}
tbody th {
  font-weight: 400;
  cursor: pointer;
}
.title_wrapper {
  min-height: 80px;
}
.input_field {
  padding-left: 3px;
  /* min-width: 60%; */
  text-align: left;
}

.flex_container,
.flex_container_spased {
  display: flex;
  align-items: center;
  min-height: 80px;
}
.flex_container_spased {
  justify-content: space-around;
  flex-wrap: wrap;
  /* max-width: 1110px; */
}

.sorting_tools {
  margin-bottom: 0;
  margin-right: 5px;
  font-weight: bold;
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 1200;
}

.Modal {
  padding: 15px;
  min-width: 250px;
  max-width: calc(100vw - 150px);
  max-height: calc(100vh - 24px);
  background-color: white;
  overflow: auto;
}
.sides_col {
  min-width: 80px;
}
.center_col {
  text-align: left;
}
.modal_title {
  color: yellow;
}
.image_wrapper {
  max-width: 200px;
  min-width: 50px;
}
img {
  display: block;
  width: 75%;
  max-height: auto;
  margin-left: auto;
  margin-right: auto;
}
